import { MyPlansComponent } from './my-plans/my-plans.component';
import { ChangePasswordComponent } from './change-password/change-password.component';
import { UpdateProfileComponent } from './update-profile/update-profile.component';
import { ViewProfileComponent } from './view-profile/view-profile.component';
import { MemberHomeComponent } from './member-home/member-home.component';
import { PaymentDetailsComponent } from './../payment-details/payment-details.component';
import { GymCartComponent } from './../gym-cart/gym-cart.component';
import { MemberComponent } from './member.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {path:'',component:MemberComponent},
  {path:'cart',component:GymCartComponent},
  {path:'payment',component:PaymentDetailsComponent},
  {path:'member-home',component:MemberHomeComponent},
  {path:'view-profile',component:ViewProfileComponent},
  {path:'update-profile',component:UpdateProfileComponent},
  {path:'cpwd',component:ChangePasswordComponent},
  {path:'my-plans',component:MyPlansComponent}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MemberRoutingModule { }
